$projectWidth: 1600px;
$projectHeight: 12568px;
$bannerBackColor: #7a7a7a;
$bannerHeight: 770px;
$paddingBlock: 220px;
$paddingFEDblock: 70px;
$sizeIconFED:69px;
$navColor: #ffffff;
