body{ 
    position:relative;
    width:$projectWidth;
    height: $projectHeight;
    margin: 0 auto;
    
}
.grayfon{
    background-color: $grayFon;
}
.paddingBlock{
    padding-left: $paddingBlock;
    padding-right: $paddingBlock;
    padding-top: 5px;
    padding-bottom: 5px;
    box-shadow: 0 7px 7px #e0e0e0;
}
.navColor{
    background-color: $navColor;
}
.banner{
    position: relative;
    background-color: $bannerBackColor;
    height: $bannerHeight;
    padding: 0;
}
.nvbar{
    position: relative;
    height: 60px;
    padding: 10px $paddingBlock;
    background-color: $navColor;
}
.logo{
    background-color: $navColor;
    
}
.nav{
    a{
        font-family: Ubuntu, sans-serif;
        font-size: 15px;
        display: inline-block;
        padding-top: 14px;
        padding-left: 12px;
        padding-right: 12px;
        float: left;
        color: $navLinkColor;
    }
    
}
.socNetwork{
    font-family: Awesome sans-serif;
    padding-left: 30px;
    padding-top: 3px;
    padding-bottom: 3px;
    a{
        position: relative;
        display: inline-block;
        height: 100%;
        
    }
   
}
.socLine{
    position: relative;
    display: inline-block;
    width: 2px;
    height: 34px;
    border: 1px solid #f3f3f3;
}
.facebook{
    color: $facebookColorIcon;
}
.twitter{
    color: $twitterColorIcon;
}
.linkedin{
    color:#3395c5;
}
.fa{
    padding-left: 10px;
}
.circlesymbol {
    position: relative;
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    color:black;
    border: 1px solid black;
}
.cir3{
    padding-left: 5px;
    padding-right: 5px;
}
.inlineblock{
    display: inline-block;
    position: relative;
    top: -6px;
}
.bblock{
    padding-top: 90px;
    padding-bottom: 95px;
    padding-left: $paddingBlock;
    padding-right: $paddingBlock;
    border-bottom: 1px solid #dddddd;
}
.cblock{
    padding-top: 90px;
    padding-bottom: 95px;
    border-bottom: 1px solid #dddddd;
}
.ich{
    font-size: 63px;
}
.prodfeatures {
    padding-top: 40px;
    font-family: ubuntuBold;
    letter-spacing: 5px;
    color: $productFeatures;
    font-size: 15px;
}
.tc{
    text-align: center;
}
.textwhatwedo{
    color: $textwhatwedo;
    font-family: ubuntuLight;
    font-size: 61px;
    padding-bottom: 90px;
    span{
      color: $textwhatweDO;  
    }
}
.blockFED{
    margin-top: $paddingFEDblock;
    h3{
        font-family: $FEDH3Font;
        font-size: $FEDH3Size;
        padding-top: 30px;
    }
    p{
        font-size: 16px;
        font-family: $FEDPFont;
        color:#939598;
        
    }
}
.fullyBlock{
    span {
        font-size: $sizeIconFED;
        color:#4796b7;
    }
}
.easyBlock{
    span {
        font-size: $sizeIconFED;
        color:#9f5cd8;
    }
}
.directBlock{
    span {
        font-size: $sizeIconFED;
        color:#f1438a;
    }
}
.servOuterBlock{
    
}
.servInnerBlock{
    margin: 15px;
    background-color: white;
    height: 250px;
    box-shadow: 0 0 7px gray;
    padding: 15px;
    &:hover{
        .ServBlockIcon{
            display: none;
        }
        .ServBlockText{
            text-align: left;
            font-weight: 700;
            padding-left: 40px;
        }
        .sbhover {
            display: block;
        }
        
    }
}
.ServBlockText{
    font-family: Ubuntu;
    font-weight: 100;
    font-size: 27px;
    padding-top: 20px;
    padding-left: 0;
    text-align: center;
}
.ServBlockIcon{
    padding-top: 20px;
    text-align: center;
    font-size: 70px;
}
.sbhover{
    margin: 5px 25px;
    padding: 8px 30px;
    font-family: UbuntuLight;
    font-size: 16px;
    box-shadow: 2px 2px 3px #e8e8e8;
    text-align: left;
    display: none;
    color: black;
    text-decoration: none;
    &:hover{
        text-decoration: none;
        color: black;
        background-color: #f2f2f2;
    }
}
.msBox{
    height: 500px;
}
#slider{
    margin: 0 10px;
    background-color: chartreuse;
    color: black;
    position: relative;
    width: 100%;
    height: 500px;
} 
#messageAccordion{
    margin: 10px;
    box-shadow: 0 0 3px 1px #dcdddf;
    text-align: left;
    
    h3{
        padding: 35px 20px;
        margin: 0;
        font-family: UbuntuLight;
        font-size: 20px;
        color: #ddd;
        border: 5px solid transparent;
        border-bottom: 1px solid #dcdddf;
        
        
    }
    .ui-accordion-header-active{
        font-size: 20px;
        color:black;
    }
    div{
        padding: 10px 20px;
        border: 5px solid transparent;
        border-bottom: 1px solid #dcdddf;
    }
}
.messageNotice{
    margin: 10px;
    font-family: Ubuntu-R;
    color: #9b9da0;
    position: absolute;
    bottom: 0;
}
.tabOurWorks{
    display: inline-block;
    padding: 18px 40px;
    margin: 0 1px;
    box-shadow: 0 0 5px 2px  #e9e9e8;
    font-family: 'Ubuntu-R' ,sans-serif;
    color:white;
    background-color: #1abc9c;
    font-size: 18px;
    
}
.tabOurWorksActive{
    background-color: black;
}

.tab{
    display: inline-block;
    padding: 18px 40px;
    margin: 0 1px;
    box-shadow: 0 0 5px 2px  #e9e9e8;
    border-radius: 5px 5px 0 0;
    font-family: 'Ubuntu-R' sans-serif;
    
}
.tabActive{
    color: white;
    background-color: #1abc9c;
}

#tabContent{
    padding-top: 110px;
}
#IntroContent h3{
    margin-top: 60px;
}
.tabText{
    font-family: UbuntuLight;
    text-align: left;
    margin-top: auto 0;
        
    h3{
        font-size: 27px;
        padding-right: 150px;
    }
    p{
        font-size: 16px;
        padding-top: 20px;
        color:#c9c9c9;
        
    }
}
.tbCont{
    display: none;
}
.tbContActive{
    display: block;
}
.bIcon{
    font-size: 220px;
    padding: 20px;
    
}
.worksSquard{
    position: relative;
    height: 260px;
    transition: all .2s;
    z-index: 2;

}

.block{
    position: relative;
    height: 260px;
    transition: all .2s;
    z-index: 2;
    &:hover{
        z-index: 3;
        .blHover{
            opacity: 1;
            
        }
        .underBlock{
            opacity: 1;
        }
        
    }
}
.rollUp{
    &:hover .worksSquard{
            transform: translate(0,-70px);
        }   
    
}
.rollDown{
    &:hover .worksSquard{
            transform: translate(0,70px);
        }
    
}
.blockIcon{
    font-size: 180px;
    padding:30px;
}
.underBlock{
    //opacity: 0;
    padding: 10px;
    background-color: #252830;
    color:white;
    position: absolute;
    //bottom:0;
    //transition: opacity 1s;
    z-index: 1;
    }
.rollUp{
    .underBlock{
                bottom: 0;
            }
}
.rollDown{
    .underBlock{
        top:0;
    }
}
.OurWorksLink{
    padding-bottom: 80px;
}
.blockCss{
    margin: 1px;
    padding: 5px;
    font-family: 'Ubuntu-R', sans-serif;
    font-size: 11px;
    border-radius: 5px;
    background-color: #f2ede8;
    color: #5ebd85;
}
.blHover{
    position: relative;
    opacity: 0;
    transition: all .5s;
    
}
.blockHover{
    padding: 20px;
}
.bImg{
    max-height: 130px;
    max-width: 130px;
}

.teamPerson{
    padding: 20px;
    margin: 5px;
    font-family: 'Ubuntu-R',sans-serif;
    font-size: 27px;
    &:hover{
        box-shadow: 0 0 14px 10px #e9e9e8;
    }
}
.personFoto{
    margin: 0 auto;
    width:256px;
    height:480px;
    max-width: 256px;
    max-height: 480px;
    background-color: #e9e9e8;
    margin-bottom: 20px;
}
.persPosition{
    font-size: 19px;
}
